interface ChinaMapProps {
  height?: string;
  onRegionClick?: (region: typeof regionCenters[0]) => void;
  selectedRegion?: typeof regionCenters[0] | null;
  showOffices?: boolean;
  className?: string;
}

export const ChinaMap = ({
  height = '500px',
  onRegionClick,
  selectedRegion,
  showOffices = true,
  className = ''
}: ChinaMapProps) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstance = useRef<any>(null);
  const echartsRef = useRef<any>(null);
  const [isReady, setIsReady] = useState(false);

  // åŠ¨æ€åŠ è½½ ECharts
  useEffect(() => {
    let isMounted = true;
    
    const init = async () => {
      try {
        // åŠ¨æ€å¯¼å…¥ echarts
        const echarts = await import('echarts');
        if (!isMounted) return;
        
        echartsRef.current = echarts;
        
        // åŠ è½½åœ°å›¾æ•°æ®
        const res = await fetch('/china.json');
        if (!res.ok) throw new Error('Failed to load map data');
        const geoJson = await res.json();
        
        if (!isMounted) return;
        
        // æ³¨å†Œåœ°å›¾
        if (!echarts.getMap('china')) {
          echarts.registerMap('china', geoJson);
        }
        
        // åˆå§‹åŒ–å›¾è¡¨
        if (chartRef.current) {
          chartInstance.current = echarts.init(chartRef.current);
          setIsReady(true);
        }
      } catch (err) {
        console.error('Map init error:', err);
      }
    };
    
    init();
    
    return () => {
      isMounted = false;
      if (chartInstance.current) {
        chartInstance.current.dispose();
        chartInstance.current = null;
      }
    };
  }, []);

  // æ›´æ–°å›¾è¡¨
  useEffect(() => {
    if (!isReady || !chartInstance.current || !echartsRef.current) return;
    
    const echarts = echartsRef.current;
    
    const option = {
      backgroundColor: 'transparent',
      tooltip: {
        trigger: 'item',
        backgroundColor: 'rgba(255, 255, 255, 0.95)',
        borderColor: '#e5e7eb',
        borderWidth: 1,
        textStyle: { color: '#1f2937' },
        formatter: (params: any) => {
          if (params.seriesType === 'scatter') {
            const isRegion = params.data?.type === 'region';
            return `<div style="padding: 8px;">
              <div style="font-weight: bold; font-size: 14px; color: ${isRegion ? '#059669' : '#10b981'}">${params.name}</div>
              <div style="font-size: 12px; color: #6b7280;">${isRegion ? 'å¤§åŒºä¸­å¿ƒ' : params.data?.region + 'å¤§åŒº'}</div>
            </div>`;
          }
          return params.name;
        }
      },
      geo: {
        map: 'china',
        roam: true,
        zoom: 1.8,
        center: [102, 35],
        label: { show: false },
        itemStyle: { 
          areaColor: 'rgba(5, 150, 105, 0.15)', 
          borderColor: '#ffffff', 
          borderWidth: 1 
        },
        emphasis: { 
          itemStyle: { 
            areaColor: 'rgba(5, 150, 105, 0.35)'
          } 
        }
      },
      series: [
        {
          name: 'å¤§åŒºä¸­å¿ƒ',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: regionCenters.map(item => ({
            name: item.name,
            value: [...item.value, item.city],
            type: 'region',
            itemStyle: { 
              color: selectedRegion?.name === item.name ? '#047857' : '#059669'
            }
          })),
          symbolSize: (val: any, params: any) => {
            if (params.data.name === 'ååŒ—') return 40;
            return selectedRegion?.name === params.data.name ? 32 : 26;
          },
          label: { 
            show: true, 
            formatter: '{b}', 
            position: 'bottom', 
            fontSize: 11, 
            color: '#059669', 
            fontWeight: 'bold' 
          },
          zlevel: 2
        },
        ...(showOffices ? [{
          name: 'åŠäº‹å¤„',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: offices.map(item => ({
            name: item.name,
            value: item.value,
            region: item.region,
            itemStyle: { 
              color: '#10b981',
              opacity: selectedRegion && selectedRegion.name !== item.region ? 0.3 : 0.9
            }
          })),
          symbol: 'circle',
          symbolSize: 8,
          zlevel: 1
        }] : [])
      ]
    };

    chartInstance.current.setOption(option, true);

    // äº‹ä»¶
    chartInstance.current.off('click');
    chartInstance.current.on('click', (params: any) => {
      if (params.seriesType === 'scatter' && onRegionClick) {
        const region = regionCenters.find(r => r.name === params.name);
        if (region) onRegionClick(region);
      }
    });
    
    // resize
    const handleResize = () => chartInstance.current?.resize();
    window.addEventListener('resize', handleResize);
    
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [isReady, selectedRegion, showOffices, onRegionClick]);

  return (
    <div 
      ref={chartRef} 
      style={{ width: '100%', height }} 
      className={className}
    />
  );
};

export default ChinaMap;